import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';

import { ROLE_CONFIG } from './../../config';

import * as permissionActions from './../../actions/permission.actions';
import * as fromRole from './../../reducers/role.reducer';

@IonicPage({
  name: 'roles-list-page',
  segment: 'roles',
})
@Component({
  selector: 'page-roles-list',
  templateUrl: 'roles-list.page.html'
})
export class RolesListPage implements OnInit {

  public constructor(
    public navCtrl: NavController,
    private store: Store<fromRole.ModuleState>,
  ) { }

  public ngOnInit() { }

  public createDefaultPermissions() {
    this.store.dispatch(new permissionActions.CreateDefaultPermissions(ROLE_CONFIG.default_permissions));
  }

}



